import React from 'react'
import { Modal } from 'react-bootstrap'

function DataBaseResponse(props) {

    const { open, toggalModal, modalContent } = props;

    return (
        <div>
            <Modal
                show={open}
                onHide={toggalModal}
                aria-labelledby="ModalHeader"
            >
                <Modal.Header >
                    <Modal.Title id='ModalHeader'>
                        <h2>{modalContent}</h2>
                        <button className='btn_close_icon' type='button'>
                            <img src="./images/faq/plus.png" alt="close icon" width={14} height={14} onClick={() => toggalModal()} />
                        </button>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {
                        modalContent === "appointment rejected" ?
                            (
                                <div className='response_description'>
                                    <div className='response-image' style={{ textAlign: "center", marginTop: "-40px" }}>
                                        <img src="./images/responseicone/cancel-icon-design.png" alt="canse-img" width={150} height={160} />
                                    </div>
                                    <h2>Please wait for a call since you have already submitted your appoment.</h2>
                                </div>
                            )
                            :
                            (
                                <div className='response_description'>
                                    <div className='response-image' style={{ textAlign: "center", marginTop: "-40px" }}>
                                        <img src="./images/responseicone/check.png" alt="canse-img" width={150} height={160} />
                                    </div>
                                    <h2>Your request has been submitted, we will be in touch with you shortly. Please wait for our call</h2>
                                </div>
                            )
                    }
                </Modal.Body>
                <Modal.Footer>
                    <div className='schedule_form_button btn_ok'>
                        <button className='btn btn-default' onClick={() => toggalModal()}>ok</button>
                    </div>

                </Modal.Footer>
            </Modal>
        </div>
    )
}

export default DataBaseResponse
